import { Component } from '@angular/core'
import { CommonModule } from '@angular/common'
import { IonicModule } from '@ionic/angular'
import { ModalController } from '@ionic/angular/standalone'
import { TranslateModule } from '@ngx-translate/core'
import { addIcons } from 'ionicons'
import { close } from 'ionicons/icons'
import {
  CguService,
  cguLoadingState,
} from 'src/app/services/api-wrappers/cgu.service'

@Component({
    selector: 'app-cgu',
    templateUrl: './cgu.component.html',
    styleUrls: ['./cgu.component.scss'],
    imports: [CommonModule, IonicModule, TranslateModule]
})
export class CguComponent {
  public cguLoadingState: cguLoadingState = { state: 'loading' }
  constructor(
    private modalCtrl: ModalController,
    private cguService: CguService
  ) {
    this.cguService.cguLoadingState.subscribe((state) => {
      this.cguLoadingState = state
    })
    addIcons({ close })
  }

  dismiss() {
    this.modalCtrl.dismiss()
  }
}
